:root{
    --montserrat: 'Montserrat', sans-serif;
    --color-black: #00343D;
    --color-blue: #1292EE;
    --color-light: #f5f5f5;
}

.cards{
  margin-top: 7rem;
}
  
.cards_title {	
    font-family: var(--montserrat);
    text-align: center;	
    font-weight: 500;
    font-size: 1.3em;
    color: var(--color-black);
  }	
  
  .cards__container {	
    display: flex;	
    flex-flow: column;	
    align-items: center;	
    max-width: 1120px;	
    width: 100%;	
    margin: 0 auto;	
  }	
  
  .cards__wrapper {	
    position: relative;	
    margin: 10px 0 45px;	
  }	
  
  .cards__items {	
    margin-bottom: 24px;	
    padding: 0;
  }	
  
  .cards__item {	
    display: flex;	
    flex: 1;	
    margin: 0 1rem;	
    border-radius: 10px;	
  }	
  
  .cards__item__link {	
    display: flex;	
    flex-flow: column;	
    width: 100%;	
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);	
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));	
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));	
    border-radius: 10px;	
    overflow: hidden;	
    text-decoration: none;	
  }	
  
  .cards__item__pic-wrap {	
    position: relative;	
    width: 100%;	
    padding-top: 67%;	
    overflow: hidden;	
    margin: 0;
    
  }	
  
  .fade-img {	
    animation-name: fade-img;	
    animation-duration: 2s;	
  }	
  
  .cards__item__pic-wrap::after {	
    content: attr(data-category);	
    position: absolute;	
    bottom: 0;
    left: 0;	
    margin-left: 10px;	
    padding: 6px 8px;	
    max-width: calc((100%) - 60px);	
    font-size: 12px;	
    color: #fff;	
    background-color: #1f98f4;	
    box-sizing: border-box;	
    font-family: var(--montserrat);
    text-align: left;	
    font-weight: 300;
    font-size: .7em;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }	
  
  .cards__item__img {	
    position: absolute;	
    top: 0;	
    right: 0;	
    bottom: 0;	
    left: 0;	
    display: block;	
    width: 100%;	
    max-width: 100%;	
    height: 100%;	
    max-height: 100%;	
    object-fit: contain;	
    transition: all 0.2s linear;	
  }	
  
  .cards__item__img:hover {	
    transform: scale(1.1);	
  }	
  
  .cards__item__info {	
    padding: 20px 1em;	
  }	
  
  .cards__item__text {	
    color: #252e48;	
    font-family: var(--montserrat);
    text-align: left;	
    font-weight: 500;
    font-size: 1em;	
    line-height: 24px;
    text-decoration: none;	
  }	
  .cards__item:hover{
    text-decoration: none !important;
  }
  
  @media only screen and (min-width: 1200px) {	
    .content__blog__container {	
      width: 84%;	
    }	
  }	
  
  @media only screen and (min-width: 1024px) {	
    .cards__items {	
      display: flex;	
    }	
  }	
  
  @media only screen and (max-width: 1024px) {	
    .cards__item {	
      margin-bottom: 2rem;	
    }	
  }

  @media only screen and (max-width: 576px){
    .cards_title{
      font-size: 1.1em;
    }
  }